*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-size: 18px;
  line-height: 20px;
  font-weight: 700;
  margin: 0;
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  padding: 16px 30px;
  text-align: center;
  color: #444444;
  border: 2px solid #444444;
}

.page {
  height: 100%;
}

.page__body {
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  min-height: 100%;
  margin: 0;
}

.main-header__inner {
  padding: 22px 30px;
  display: flex;
  justify-content: flex-end;
  border-bottom: 1px solid #ececec;
}

.main-header__link-bear-logo {
  margin-right: 50%;
  transform: translateX(50%);
}

.main-header__toggle {
  position: relative;
  width: 20px;
  height: 14px;
  cursor: pointer;
  border: 0;
  background-color: transparent;
}

.main-header__wrapper--open .main-header__toggle::before, .main-header__wrapper--open .main-header__toggle::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 26px;
  height: 2px;
  background-color: #231f20;
}
.main-header__wrapper--open .main-header__toggle::before {
  transform: rotate(45deg);
}
.main-header__wrapper--open .main-header__toggle::after {
  transform: rotate(-45deg);
}

.main-header__wrapper--closed .main-header__toggle::before {
  content: "";
  position: absolute;
  top: 55%;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: #231f20;
  box-shadow: 0 8px 0 0 #231f20, 0 16px 0 0 #231f20;
}

.main-header__wrapper--nojs .main-header__toggle {
  display: none;
}

.main-header__wrapper--nojs .main-header__main-nav-list {
  position: static;
}

@media (max-width: 767px) {
  .main-header__wrapper--closed .main-header__main-nav-item {
    display: none;
  }
}
.main-header__wrapper--nojs .main-header__main-nav-item {
  display: block;
}

.main-nav {
  position: relative;
}

.main-nav__list {
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  margin: 0;
  list-style-type: none;
  padding: 0;
  position: absolute;
  z-index: 1;
  background-color: #ffffff;
  width: 100%;
}

.main-nav__link {
  position: relative;
  text-decoration: none;
  color: #000000;
}

.main-nav__item {
  padding: 28px 40px 26px 76px;
  border-bottom: 1px solid #ececec;
}

.main-nav__item--reset-border {
  border-bottom: none;
}

.main-nav__link-icon {
  position: absolute;
  left: -40px;
  top: 50%;
  transform: translateY(-9px);
}

.promo__wrapper {
  padding: 73px 30px 120px;
  background: url("../img/hero-mobile1.jpg") no-repeat;
  background-position: center -16px;
  background-size: 568px 373px;
}

.promo__title {
  font-size: 27px;
  line-height: 30px;
  font-weight: 700;
  margin: 0;
  text-align: center;
}

.promo__thing {
  font-size: 19px;
  line-height: 24px;
  font-weight: 700;
  margin: 0;
  position: relative;
  display: block;
  min-height: 103px;
  padding: 24px 130px 31px 30px;
}
.promo__thing::before {
  position: absolute;
  content: "";
  background-size: contain;
}

.promo__link {
  color: #ffffff;
  text-decoration: none;
  border: none;
  padding: 0;
  text-align: left;
}

.promo__thing--toys {
  background-color: #56c3b2;
}
.promo__thing--toys::before {
  width: 60px;
  height: 60px;
  background-image: url("../img/interior-decor/toy.svg");
  background-size: contain;
  right: 27px;
}

.promo__thing--interior {
  background-color: #62d1ba;
}
.promo__thing--interior::before {
  width: 40px;
  height: 62px;
  background-image: url("../img/interior-decor/lamp.svg");
  right: 36px;
}

.contact {
  padding-top: 38px;
  padding-bottom: 39px;
}

.contact__wrapper {
  padding: 0 30px 62px;
}

.contact__title {
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 28px;
}

.contact__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  color: #444444;
}

.contact__item {
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  margin: 0;
  display: flex;
  margin-bottom: 25px;
}

.contact__name {
  flex-basis: 30%;
}

.contact__map {
  margin-bottom: 41px;
}

.contact__link {
  text-decoration: none;
  color: inherit;
}

.contact__info {
  flex-basis: 70%;
  margin: 0;
}

.contact__link--email {
  flex-basis: auto;
  border-bottom: 1px solid #62d1ba;
}

.contact__box {
  padding: 0 30px;
}

.map {
  width: 100%;
  border: 0;
  height: 454px;
}

.best-product {
  position: relative;
}

.best-product__inner {
  padding: 31px 30px 36px;
}

.best-product__inner::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  width: 100%;
  display: block;
  height: 6px;
  background: url("../img/zigzag-line.png") repeat;
}

.best-product__title {
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 3px;
}

.best-product__subtitle {
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  margin: 0;
  color: #62d1ba;
  text-transform: uppercase;
  margin-bottom: 17px;
}

.best-product__description {
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  margin: 0;
  margin-bottom: 28px;
}

.best-product__list-box {
  display: grid;
  grid-template-columns: 105px auto;
  padding: 10px 0 10px;
  border-bottom: solid 1px #ececec;
}

.best-product__list {
  margin-bottom: 27px;
}

.best-product__value {
  margin: 0;
}

.best-product__image {
  display: block;
  margin: 0 auto;
  margin-bottom: 25px;
}

.best-product__text {
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  margin: 0;
  text-align: center;
  margin-bottom: 24px;
}

.about-us__inner {
  padding: 31px 30px 71px;
}

.about-us__title {
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 28px;
}

.about-us__list {
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  row-gap: 30px;
}

.about-us__item {
  padding-top: 54px;
  position: relative;
  flex-basis: auto;
  width: 100%;
  max-width: 100%;
}

.about-us__item--flower-icon::before,
.about-us__item--money-icon::before,
.about-us__item--heart-icon::before,
.about-us__item--ball-icon::before,
.about-us__item--rocket-icon::before,
.about-us__item--gift-icon::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 55px;
  height: 40px;
  background-repeat: no-repeat;
}

.about-us__item--flower-icon::before {
  background-image: url("../img/icons-about-us/flower.svg");
  background-size: 33px 38px;
}

.about-us__item--money-icon::before {
  background-image: url("../img/icons-about-us/money.svg");
  background-size: 47px 27px;
}

.about-us__item--heart-icon::before {
  background-image: url("../img/icons-about-us/heart.svg");
  background-size: 34px 27px;
}

.about-us__item--ball-icon::before {
  background-image: url("../img/icons-about-us/ball.svg");
  background-size: 35px 34px;
}

.about-us__item--rocket-icon::before {
  background-image: url("../img/icons-about-us/rocket.svg");
  background-size: 55px 23px;
}

.about-us__item--gift-icon::before {
  background-image: url("../img/icons-about-us/gift.svg");
  background-size: 33px 32px;
}

.reviews__wrapper {
  position: relative;
  padding: 0 30px 45px;
}
.reviews__wrapper::before {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  width: 100%;
  display: block;
  height: 6px;
  background: url("../img/zigzag-line.png") repeat;
}

.reviews__title {
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 14px;
}

.reviews__item {
  font-size: 15px;
  line-height: 30px;
  font-weight: 400;
  margin: 0;
  color: #444444;
}

.reviews__toggle {
  position: relative;
  border: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 54px;
}

.reviews__button {
  position: relative;
  min-height: 52px;
  padding: 13px 42px 13px 38px;
  display: flex;
  justify-content: center;
  background: #ffffff;
  border: 1px solid #ececec;
  cursor: pointer;
}

.reviews__arrow {
  position: absolute;
}

.reviews__arrow--right {
  transform: rotate(180deg);
}

.reviews__info {
  margin: 0;
  margin-bottom: 33px;
}

.reviews__author {
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
}

.reviews__author-name {
  font-size: 17px;
  line-height: 30px;
  font-weight: 700;
  margin: 0;
  color: #000000;
}

.reviews__author-contact {
  font-size: 17px;
  line-height: 24px;
  font-weight: 400;
  margin: 0;
}

.main-footer__wrapper {
  padding: 25px 30px 31px;
}

.main-footer__link {
  font-size: 19px;
  line-height: 18px;
  font-weight: 700;
  margin: 0;
  text-decoration: none;
  color: #000000;
  cursor: pointer;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 27px;
}

.social {
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: center;
  column-gap: 29px;
  flex-wrap: wrap;
}

.modal {
  position: fixed;
  inset: 0;
  display: none;
  z-index: 100;
  background-color: rgba(255, 255, 255, 0.8);
}

.modal__main {
  min-width: 320px;
  background-color: #ffffff;
  margin: auto;
  padding: 30px 30px 46px;
}

.modal__title {
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 14px;
  display: flex;
  justify-content: center;
  text-transform: uppercase;
}

.modal__button-close {
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  margin: 0;
  min-width: 260px;
  background-color: #62d1ba;
  color: #ffffff;
  border: none;
}

.modal__paragraph {
  font-size: 15px;
  line-height: 30px;
  font-weight: 400;
  margin: 0;
  margin-bottom: 21px;
  text-align: center;
  color: #444444;
}

.size-form__innder {
  display: flex;
  column-gap: 7px;
  margin-bottom: 31px;
}

.size-form__checkbox {
  min-width: 82px;
  border: 2px solid rgba(68, 68, 68, 0.1);
}

.checkbox__label {
  cursor: pointer;
}

.checkbox__label--size {
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  margin: 0;
  color: #444444;
  display: block;
  width: 100%;
  padding: 18px 10px;
  text-align: center;
  text-transform: uppercase;
}